import Vue from 'vue';
import AlertVue from './main.vue';

const AlertConstructor = Vue.extend(AlertVue);

let Alert = {};
Alert.zIndex = 1;
Alert.installed = false;
Alert.install = function(Vue) {
  if (Alert.installed) {
    return;
  }
  let instance = null;
  Vue.prototype.$alert = (options = '') => {
    let message = typeof options === 'string' ? options : options.message; // 初始化需要显示文本 因为支持直传文本
    let type = options.type || 'info'; // 初始化显示时间 默认是2500

    if (type !== 'info' && type !== 'warn' && type !== 'error' && type !== 'ok') {
      type = 'info';
      console.warn('Alert 类型必须为以下四种之一:info、warn、error、ok');
      return;
    }
    if (!instance) {
      instance = new AlertConstructor().$mount(document.createElement('div')); // 实例化并挂载到新建的div上
    }
    document.body.appendChild(instance.$el);

    instance.type = type;
    instance.message = message;

    instance.show();
    window.addEventListener('hashchange', instance.hide);
  };
  Alert.installed = true;
};

export { Alert };
