import Vue from 'vue';
import notifyVue from './main.vue';

const NotifyConstructor = Vue.extend(notifyVue);

let Notify = {};
Notify.zIndex = 1;
Notify.installed = false;
Notify.install = function(Vue) {
  if (Notify.installed) {
    return;
  }

  Vue.prototype.$notify = options => {
    let message = typeof options === 'string' ? options : options.message; // 初始化需要显示文本 因为支持直传文本
    let autoClose = options.autoClose || true; // 初始化显示时间 默认是2500
    let duration = options.duration || 2500; // 初始化显示时间 默认是2500

    let instance = new NotifyConstructor().$mount(document.createElement('div')); // 实例化并挂载到新建的div上
    document.body.appendChild(instance.$el);

    instance.timeout = 0;
    instance.message = message;
    instance.duration = duration;
    instance.autoClose = autoClose;
    Notify.zIndex++;
    instance.zIndex = Notify.zIndex;
    instance.show();
    autoClose = false;
    if (autoClose) {
      Vue.nextTick(() => {
        instance.timeout = setTimeout(function() {
          Notify.zIndex--;
          instance.hide();
        }, duration);
      });
    }
  };
  Notify.installed = true;
};

export { Notify };
