<template>
  <transition name="alert-fade">
    <div class="pop-alert" v-if="visible" @click.self="handleHide" @touchmove.prevent>
      <div class="border">
        <div class="content">
          <i class="icon-close" @click="handleHide"><img src="data:image/png;base64,"></i>
          <span class="message" :class="'msg-'+type" v-html="message"></span>
        </div>
      </div>
    </div>
  </transition>
</template>
<script>
export default {
  data() {
    return {
      type: 'error',
      message: '',
      visible: false,
      timer: null,
      oldHash: window.location.hash
    };
  },
  computed: {
  },
  methods: {
    handleHide() {
      this.hide();
    },
    show() {
      this.visible = true;
    },
    hide() {
      this.visible = false;
      clearInterval(this.timer);
    },
    checkRouter() {
      this.timer = setInterval(() => {
        if (window.location.hash !== this.oldHash) {
          this.hide();
        }
      }, 100)
    }
  },
  mounted() {
    this.checkRouter();
  }
};
</script>
