import Vue from 'vue';
import VueRouter from 'vue-router';
import {
  home,
  blogsHome,
  blogsIndex,
  archives,
  categories,
  tags,
  about,
  posts
} from './routers'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'home',
    component: home,
    meta: { title: 'lm blogs' }
  },
  {
    path: '/blogs',
    component: blogsHome,
    children: [
      {
        path: '/',
        name: 'blogs',
        component: blogsIndex,
        meta: { title: '首页 - lm' }
      },
      {
        path: '/archives',
        name: 'archives',
        component: archives,
        meta: { title: '归档 - lm' }
      },
      {
        path: '/categories',
        name: 'categories',
        component: categories,
        meta: { title: '分类 - lm' }
      },
      {
        path: '/tags',
        name: 'tags',
        component: tags,
        meta: { title: '标签 - lm' }
      },
      {
        path: '/about',
        name: 'about',
        component: about,
        meta: { title: '关于 - lm' }
      }
    ]
  },
  {
    path: '/posts',
    name: 'posts',
    component: posts,
    meta: { title: 'posts - lm'}
  }
]

const router = new VueRouter({
  base: process.env.BASE_URL,
  routes
})

export default router
