import Vue from 'vue';
import ConfirmVue from './main.vue';

const ConfirmConstructor = Vue.extend(ConfirmVue);

let Confirm = {};
Confirm.zIndex = 1;
Confirm.installed = false;
Confirm.install = function(Vue) {
  if (Confirm.installed) {
    return;
  }

  Vue.prototype.$confirm = options => {
    let message = typeof options === 'string' ? options : options.message; // 初始化需要显示文本 因为支持直传文本
    let title = options.title;
    let cancelTxt = options.cancelTxt;
    let confirmTxt = options.confirmTxt;
    let cancelFunc = options.cancel;
    let confirmFunc = options.confirm;
    let isShowTitle = options.isShowTitle;
    let useConfirm = options.useConfirm;

    let instance = new ConfirmConstructor().$mount(document.createElement('div')); // 实例化并挂载到新建的div上
    document.body.appendChild(instance.$el);
    instance.message = message;
    if (title) {
      instance.title = title;
    }
    if (useConfirm !== undefined) {
      instance.useConfirm = useConfirm;
    }
    if (isShowTitle !== undefined) {
      instance.isShowTitle = isShowTitle;
    }
    if (cancelTxt) {
      instance.cancelTxt = cancelTxt;
    }
    if (confirmTxt) {
      instance.confirmTxt = confirmTxt;
    }
    if (cancelFunc) {
      instance.cancel = cancelFunc;
    }
    if (confirmFunc) {
      instance.confirm = confirmFunc;
    }
    instance.show();
  };
  Confirm.installed = true;
};

export { Confirm };
